"use client";
import Link from "next/link";
import { ShoppingBag, Menu, X, ArrowRight } from "lucide-react";
import { useState } from "react";

interface NavbarProps {
  cartCount: number;
  onOpenCart: () => void;
}

export default function Navbar({ cartCount, onOpenCart }: NavbarProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="border-b border-gray-100 sticky top-0 bg-white/80 backdrop-blur-xl z-50">
      <div className="max-w-7xl mx-auto px-4 h-16 flex items-center justify-between">
        
        {/* LOGO */}
        <Link href="/" className="flex items-center gap-2 group">
          <div className="bg-gray-900 text-white w-8 h-8 rounded-lg flex items-center justify-center group-hover:bg-emerald-600 transition duration-300">
            <ShoppingBag size={16} />
          </div>
          <span className="font-extrabold text-lg tracking-tight text-gray-900">StoreLink</span>
        </Link>

        {/* Desktop Links */}
        <div className="hidden md:flex items-center gap-6">
           <Link href="/login" className="text-sm font-bold text-gray-600 hover:text-gray-900">Log In</Link>
           <Link href="/onboarding" className="flex items-center gap-2 px-5 py-2 bg-gray-900 text-white rounded-full text-sm font-bold hover:bg-gray-800 transition">
             Start Selling <ArrowRight size={14} />
           </Link>
           {cartCount > 0 && (
             <button onClick={onOpenCart} className="relative p-2 hover:bg-gray-100 rounded-full transition">
               <ShoppingBag size={20} className="text-gray-900" />
               <span className="absolute top-0 right-0 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full border-2 border-white">
                 {cartCount}
               </span>
             </button>
           )}
        </div>

        {/* Mobile Toggle */}
        <div className="flex items-center gap-3 md:hidden">
          {cartCount > 0 && (
             <button onClick={onOpenCart} className="relative p-2">
               <ShoppingBag size={22} />
               <span className="absolute top-0 right-0 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full border-2 border-white">
                 {cartCount}
               </span>
             </button>
           )}
          <button onClick={() => setIsMenuOpen(!isMenuOpen)} className="p-1">
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>
      
      {/* Mobile Menu */}
      {isMenuOpen && (
        <div className="md:hidden border-t border-gray-100 bg-white p-4 flex flex-col gap-4 animate-in slide-in-from-top-2 absolute w-full shadow-xl">
           <Link href="/login" className="block w-full py-3 text-sm font-bold text-gray-600 border-b border-gray-50">Log In to Dashboard</Link>
           <Link href="/onboarding" className="block w-full py-3 bg-gray-900 text-white rounded-xl text-sm font-bold text-center">
             Create My Store
           </Link>
        </div>
      )}
    </nav>
  );
}